* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif; 
}


.section-two {
  height: 100vh;
  background-image: linear-gradient(rgba(5, 10, 5, 0.80), rgba(90, 40, 90, 0.80));
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 20px;
}

.Section {
  height: 100vh;
}

/*Sections scroll*/

html {
  scroll-behavior: smooth;
}

/*Nav Bar*/

.img_logo {
  display: flex;
}

.img_logo img {
  height: 40px;
  margin: 0 auto;
}

.img_logo b {
  color: white;
  font-family: "Segoe UI";
  margin-top: 10px;
  font-size: 1rem;
  margin-left: 20px;
}

.section-main{
  position: relative;
  width: 100%;
  min-height: 100vh;
  /*background: linear-gradient(to left, rgba(0,0,0,.8), rgba(0,0,0,.1)),url(https://wallpaperbat.com/img/55242-anime-boy-wallpaper-top-free-anime-boy-background.png) no-repeat;*/
  
  background: linear-gradient(to left, rgba(0,0,0,.8), rgba(0,0,0,.1)),url(http://www.solofondos.com/wp-content/uploads/2016/10/cubo-de-rubik-artistica-papel-pintado-2560x960-39821_87.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 80px;
}

.section-main h1{
  color: rgba(255, 255, 255, 0.8);
  font-size:60px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height:  80p;
  text-shadow:  0 0 3px #fff, 0 0 3px #000;
}

header {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 80px;
  transition: 0.6s;
}

header.down{
  background: #000000;
  padding: 15px 80px;
}

/*header .img_logo {
  color: #fff;
  font-size:30px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
}*/

header .menu{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

header .menu ul {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

header .menu ul li {
  list-style: none;
  display: inline-block;
  margin: 0 20px;
  position: relative;

}

header .menu ul li a {
  text-decoration: none;
  color: white;
  font-family: "Segoe UI";
  font-size: 16px/*1.2rem*/;
  text-transform: uppercase;
  font-weight: 500/*bold*/;
  /*margin: 0 30px; /*Si queda mal quitarlo pto*/
  /*padding: 7px 12px; /*Si queda mal quitarlo pto*/
  /*border-radius: 20px;
  transition: 3s;
  transition-property: color, background;*/
}

header ul li::before {
  content: "";
  height: 3px;
  width: 0%;
  background: yellowgreen;
  position: absolute;
  left: 0;
  bottom: -12px;
  transition: 0.4s ease-out;
}

header ul li:hover::before {
  width: 100%;
}

header .btn{
  color: #fff;
  font-size: 25px;
  cursor: pointer;
  display: none;
}

@media (max-width: 1280px) {
  header, header.down{
    padding: 20px 40px;
  }
  
  header .btn{
    display: block;
  }

  header .menu{
    position: fixed;
    background: #000000;
    min-width: 33px;
    height: 100vh;
    top: 0;
    right: -100%;
    padding: 80px 50px;
    transition: 0.5s;
    transition-property: right;
  }

  header .menu ul{
    flex-direction: column;
  }

  header .menu.active{
    right:0;
  }

  header .menu .close-btn{
    position: absolute;
    top: 0;
    left: 0;
    margin: 25px;
  }

  header .menu a{
    display: block;
    
  }

  header .menu ul li{
    margin: 0 0 20px 0;
  }
}

@media (max-width:600px){
  .section-main h1{
    font-size: 40px;
    line-height: 60px;
  }
}
